/**
 * vuex
 */

import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

//
var state={
	count:6
}

//getters
var getters={
	count(state){
		return state.count;
	},
	isEvenOrOdd(state){
		return state.count%2==0?'':'';
	}
}

//actions
const actions = {
	increment(context){//commitdispatchstate
		console.log(context);
		// context.commmit()
	},
	// increment({commit,state}){
	// 	commit('increment'); //increment
	// },
	decrement({commit,state}){
		if(state.count>10){
			commit('decrement');
		}
	},
	incrementAsync({commit,state}){
		//
		var p=new Promise((resolve,reject) => {
			setTimeout(() => {
				resolve();
			},3000);
		});

		p.then(() => {
			commit('increment');
		}).catch(() => {
			console.log('');
		});
	}
}

//mutations
const mutations={
	increment(state){
		state.count++;
	},
	decrement(state){
		state.count--;
	}
}

//store
const store=new Vuex.Store({
	state,
	getters,
	actions,
	mutations
})

//store
export default store;



